*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  font-family: var(--inter-font);
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  color: #000;
  overflow-x: hidden;
  background: url("../images/content/img.png") no-repeat center;
  background-size: cover; }

button {
  outline: none;
  border: none; }
  button[disabled] {
    background-color: #727272;
    pointer-events: none; }

a:active,
a:hover,
a:focus {
  outline: none; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

button {
  cursor: pointer; }

.content {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 100%;
  overflow: hidden; }

.logo {
  position: relative;
  display: inline-block;
  margin-top: 5px; }
  .logo::before {
    content: "";
    position: absolute;
    top: 0;
    left: -35px;
    width: 120%;
    height: 100%;
    background: #fff;
    border-radius: 45px;
    filter: blur(15px);
    z-index: -1; }
  @media (max-width: 1365px) {
    .logo {
      margin-top: 24px;
      left: 50%;
      transform: translateX(-50%); } }

.container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px; }
  @media (max-width: 1365px) {
    .container {
      max-width: 320px;
      padding-left: 15px;
      padding-right: 15px; } }

.form-group {
  position: relative;
  margin-bottom: 12px; }
  .form-group:last-child {
    margin-bottom: 0; }

.input-group {
  position: relative; }
  .input-group--required input {
    border: 1px solid red; }
  .input-group--required .select-selected {
    border: 1px solid red; }
  .input-group--required label[for="file"] span {
    border: 1px solid red; }

.s-hidden {
  display: none; }

.styledSelect {
  position: relative;
  padding: 11px 20px;
  border: 1px solid var(--green-color);
  box-sizing: border-box;
  border-radius: 2px;
  font-size: 14px;
  color: var(--grey-color);
  text-align: left;
  cursor: pointer; }
  .styledSelect::before {
    content: "";
    position: absolute;
    right: 20px;
    top: 16px;
    width: 13px;
    height: 8px;
    background: url("../images/icons/arr.svg") no-repeat center;
    transition: all .3s linear; }
  .styledSelect.active::before {
    transform: rotate(-180deg); }

.options {
  display: none;
  position: absolute;
  top: 45px;
  left: 0;
  right: 0;
  border-radius: 2px;
  border: 1px solid var(--green-color);
  background-color: #fff;
  overflow-y: auto;
  max-height: 150px;
  z-index: 10; }
  .options li {
    padding: 11px 20px;
    font-size: 14px;
    color: var(--grey-color);
    text-align: left;
    cursor: pointer; }
    .options li:hover {
      background-color: var(--green-color);
      color: #fff; }

.btn {
  width: 100%;
  height: 56px;
  background: var(--pink-color);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  font-weight: bold;
  font-size: 18px;
  color: #fff;
  line-height: 56px;
  text-transform: uppercase;
  transition: all .3s linear; }
  .btn:active {
    box-shadow: none;
    transition: all .3s linear; }

input {
  width: 100%;
  padding: 11px 20px;
  border-radius: 2px;
  border: 1px solid var(--green-color);
  background-color: #fff;
  font-size: 14px;
  color: var(--grey-color); }
  input:focus {
    outline: var(--lightgreen-color); }

.overflow {
  overflow: hidden !important; }

.is-hidden {
  display: none !important; }

@media (max-width: 1365px) {
  .for-desktop {
    display: none !important; } }

@media (max-width: 1025px) {
  .for-mobile {
    display: none !important; } }

.clearfix {
  overflow: auto; }
  .clearfix::after {
    content: "";
    display: table;
    clear: both; }

.wrapper {
  position: relative;
  display: flex;
  height: calc(100vh - 87px);
  align-items: center;
  flex: 0 0 100%; }
  @media (max-width: 1365px) {
    .wrapper {
      flex-direction: column;
      height: auto;
      padding-bottom: 72px;
      padding-top: 14px; } }
  .wrapper__block {
    position: relative;
    display: flex;
    align-items: flex-start; }
    @media (max-width: 1365px) {
      .wrapper__block {
        flex-direction: column; } }
  .wrapper__info {
    margin-right: 143px; }
    @media (max-width: 1365px) {
      .wrapper__info {
        margin-right: 0; } }
  .wrapper__title {
    margin-top: 0;
    margin-bottom: 63px;
    font-weight: 900;
    font-size: 56px;
    font-size: calc(32px + (56 - 32) * ((100vw - 1200px) / (1920 - 1200)));
    line-height: 68px;
    line-height: calc(40px + (68 - 40) * ((100vw - 1200px) / (1920 - 1200)));
    text-transform: uppercase;
    color: var(--green-color);
    text-shadow: 2px 2px 0px #fff; }
    @media (max-width: 1200px) {
      .wrapper__title {
        font-size: 32px; } }
    @media (min-width: 1920px) {
      .wrapper__title {
        font-size: 56px; } }
    @media (max-width: 1200px) {
      .wrapper__title {
        line-height: 40px; } }
    @media (min-width: 1920px) {
      .wrapper__title {
        line-height: 68px; } }
    @media (max-width: 1365px) {
      .wrapper__title {
        text-align: center;
        margin-bottom: 30px; } }
  .wrapper__lists {
    width: 55%; }
    @media (max-width: 1365px) {
      .wrapper__lists {
        width: 300px;
        margin: 0 auto 27px auto; } }
  .wrapper__list {
    display: flex;
    align-items: flex-start;
    margin-bottom: 49px; }
    .wrapper__list:last-child {
      margin-bottom: 0; }
    @media (max-width: 1365px) {
      .wrapper__list {
        margin-bottom: 20px; } }
    .wrapper__list svg {
      display: inline-block;
      margin-right: 21px;
      width: 54px;
      height: 50px;
      flex: 0 0 54px; }
    .wrapper__list span {
      font-weight: bold;
      font-size: 20px;
      font-size: calc(18px + (20 - 18) * ((100vw - 1365px) / (1920 - 1365)));
      line-height: 28px;
      line-height: calc(24px + (28 - 24) * ((100vw - 1365px) / (1920 - 1365)));
      color: var(--grey-color); }
      @media (max-width: 1365px) {
        .wrapper__list span {
          font-size: 18px; } }
      @media (min-width: 1920px) {
        .wrapper__list span {
          font-size: 20px; } }
      @media (max-width: 1365px) {
        .wrapper__list span {
          line-height: 24px; } }
      @media (min-width: 1920px) {
        .wrapper__list span {
          line-height: 28px; } }
  .wrapper__img {
    display: block;
    position: absolute;
    bottom: 22px;
    left: 41%; }
    @media (max-width: 1365px) {
      .wrapper__img {
        position: unset;
        margin: 0 auto; } }

.form {
  position: relative;
  width: 368px;
  padding: 24px 57px;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  text-align: center; }
  @media (max-width: 1365px) {
    .form {
      width: 100%;
      max-width: 320px;
      margin-top: 74px;
      padding: 24px 34px; } }
  .form__title {
    margin-top: 0;
    margin-bottom: 13px;
    font-weight: bold;
    font-size: 28px;
    line-height: 32px;
    color: var(--grey-color);
    letter-spacing: -0.01em; }
    @media (max-width: 1365px) {
      .form__title {
        margin-bottom: 45px; } }
  .form__desc {
    margin-top: 0;
    margin-bottom: 2px;
    font-weight: bold;
    font-size: 22px;
    color: var(--pink-color);
    letter-spacing: -0.01em; }
  .form__price {
    font-weight: 800;
    font-size: 38px;
    color: var(--pink-color); }
    .form__price font {
      font-family: 'ALS Rubl', sans-serif; }
  .form__btn {
    margin-top: 21px;
    margin-bottom: 16px; }
  .form__tooltip {
    font-size: 12px;
    line-height: 20px;
    color: var(--darkgrey-color); }
  .form form {
    padding-top: 92px; }
    @media (max-width: 1365px) {
      .form form {
        padding-top: 58px; } }
